.root {
	position: relative;
	display: block;
	margin: 10px auto;
	overflow: hidden;
	width: 290px;
	height: 200px;
}

@media (min-width: 480px) {
	.root {
		width: 435px;
		height: 300px;
	}
}

@media (min-width: 680px) {
	.root {
		width: 580px;
		height: 400px;
	}
}

.starContainer {
	position: absolute;
	top: 0;
	right: 0;
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.star {
	margin-left: 5px;
	color: white;
}

.count {
	margin-left: 5px;
	margin-right: 5px;
	color: white;
}

.container {
	overflow: hidden;
	margin: auto;
	background: #030c17;
}

.imageContainer {
	overflow: hidden;
	-webkit-transition: -webkit-transform 0.3s;
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
	-webkit-transition-delay: 0.3s;
	        transition-delay: 0.3s;
	background: black;
}

.figCaption {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	-webkit-transition: -webkit-transform 0.3s;
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
	-webkit-transition-delay: 0.3s;
	        transition-delay: 0.3s;
}

.failed .figCaption {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	-webkit-transition: -webkit-transform 0.3s;
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
	-webkit-transition-delay: 0.3s;
	        transition-delay: 0.3s;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.retryButton {
	color: white;
	background: none;
	border: none;
	font-size: 40px;
	z-index: 10;
	cursor: pointer;
}

.failed .figCaption::before {
	position: absolute;
	background: rgba(255, 0, 0, .3);
	top: 20px;
	right: 20px;
	bottom: 20px;
	left: 20px;
	border: 2px solid #fff;
	-webkit-box-shadow: 0 0 0 30px rgba(255, 255, 255, .2);
	        box-shadow: 0 0 0 30px rgba(255, 255, 255, .2);
	content: '';
	opacity: 0;
	-webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
	transition: opacity 0.35s, -webkit-transform 0.35s;
	transition: opacity 0.35s, transform 0.35s;
	transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
	-webkit-transition-delay: 0.3s;
	        transition-delay: 0.3s;
	-webkit-transform: scale3d(1.4,1.4,1);
	        transform: scale3d(1.4,1.4,1);
}

.figCaption::before {
	position: absolute;
	background: rgba(1, 1, 1, .3);
	top: 20px;
	right: 20px;
	bottom: 20px;
	left: 20px;
	border: 2px solid #fff;
	-webkit-box-shadow: 0 0 0 30px rgba(255, 255, 255, .2);
	        box-shadow: 0 0 0 30px rgba(255, 255, 255, .2);
	content: '';
	opacity: 0;
	-webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
	transition: opacity 0.35s, -webkit-transform 0.35s;
	transition: opacity 0.35s, transform 0.35s;
	transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
	-webkit-transition-delay: 0.3s;
	        transition-delay: 0.3s;
	-webkit-transform: scale3d(1.4,1.4,1);
	        transform: scale3d(1.4,1.4,1);
}

.textContainer {
	-webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
	transition: opacity 0.35s, -webkit-transform 0.35s;
	transition: opacity 0.35s, transform 0.35s;
	transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
	-webkit-transition-delay: 0.3s;
	        transition-delay: 0.3s;
	opacity: 0;
	position: absolute;
	top: 30px;
	right: 30px;
	bottom: 30px;
	left: 30px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}

.header {
	color: white;
	text-align: center;
	font-size: 18px;
}

@media (min-width: 480px) {
	.header {
		font-size: 24px;
	}
}

.active .textContainer {
	background-color: rgba(58, 52, 42, 0);
	-webkit-transform: scale3d(1,1,1);
	        transform: scale3d(1,1,1);
	opacity: 1;
}

.active .figCaption::before {
	opacity: 1;
    -webkit-transform: scale3d(1,1,1);
            transform: scale3d(1,1,1);
}

.active .figCaption {
	background-color: rgba(58, 52, 42, 0);
	opacity: 1;
}

.active.failed .figCaption::before {
	opacity: 1;
    -webkit-transform: scale3d(1,1,1);
            transform: scale3d(1,1,1);
}

.active.failed .figCaption {
	background-color: rgba(58, 52, 42, 0);
	opacity: 1;
}

.image {
	width: 290px;
	height: 200px;
	display: block;
	margin: auto;
}

@media (min-width: 480px) {
	.image {
		width: 435px;
		height: 300px;;
	}
}

@media (min-width: 680px) {
	.image {
		width: 580px;
		height: 400px;
	}
}



